import React from 'react';
import './App.css';
import {ImageParallax} from './ImageParallax/ImageParallax'
import {AboutMe} from './AboutMe/aboutme'
import {ImageGallery} from './ImageGallery/ImageGallery'
import {ContectMe} from './Contectme/ContectMe'
import {Header} from './Header/Header'
const style =
{
  textAlign:(navigator.language==="he")?"right":"left"
}
 const adrasses = {
  local: {
    url:"http://localhost:3001"
  },
  cloud: {
   url:"http://165.22.206.70:3001"
  },
};
const Url = adrasses.local;

export class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { 
      photos: []
      
     }
     this.getPhotos = this.getPhotos.bind(this);
     this.getPhotos();
  }
  componentWillMount() {
   this.getPhotos();
}

  getPhotos() {
    fetch(Url.url+'/mainImages')
      .then(response => response.json())
      .then(data => this.setState({ 
          photos:data,
        }));
  }

  render() { 
    return ( 
      <div>
    {   this.state.photos.length>0?
          <div className="App" style={style}>
        <Header fetchUrl ={Url}/>
        <ImageParallax url = {this.state.photos[0]["first"]["url"]} title={true} fetchUrl ={Url} />
        <AboutMe fetchUrl ={Url}/>
        <ImageParallax url = {this.state.photos[1]["second"]["url"]} title={false} fetchUrl ={Url} />
        <ImageGallery fetchUrl ={Url}/>
        <ImageParallax url = {this.state.photos[2]["third"]["url"]} title={false} fetchUrl ={Url} />
        <ContectMe fetchUrl ={Url}/>
        </div>:
        <div></div>
      }
      </div>
    
     );
  }
}
