import './Header.css';
import Navbar from 'react-bootstrap/Navbar';
import NavLink from 'react-bootstrap/NavLink'
import React, { Component } from 'react';
import Nav from 'react-bootstrap/Nav'
import {ArticlesPopUp} from '../ArticlesPopUp/ArticlesPopUp'
import NavDropdown from 'react-bootstrap/NavDropdown'

export class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            Articles: [],
            currentArticleTitle :"",
            currentArticleBody :"",
            visableArticle : false
          };
          this.loadArticle = this.loadArticle.bind(this);
          this.componentDidMount();

    }
componentDidMount() {
    fetch(this.props.fetchUrl.url+'/Articles')
      .then(response => response.json())
      .then(data => this.setState({ 
        Articles:data
        }));
    }
    loadArticle(index)
    {
        this.setState({
            visableArticle : !this.state.visableArticle,
            currentArticleTitle: this.state.Articles[index]["title"],
            currentArticleBody: this.state.Articles[index]["value"],
          });
    }

    getArticles()
    {
      var items = []
      for (let i = 0; i < this.state.Articles.length; i++) {
       items.push(
       <NavLink onClick={() => this.loadArticle(i)}>{this.state.Articles[i]["key"]}</NavLink>
       );
      }
     return items;
    }

   

render() {
  return (
      <div   >
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark"  >
    <Navbar.Toggle aria-controls="responsive-navbar-nav" />
    <Navbar.Collapse id="responsive-navbar-nav" >
      <Nav>
          <NavLink href="#AboutMe">לגביי</NavLink>
          <NavLink href="#Gallery">גלרייה</NavLink>
          <NavLink href="#contectMe">צרו איתי קשר</NavLink>
          <NavDropdown className="darkDropDown" title="מאמרים">
         { this.getArticles()}
          </NavDropdown>
      </Nav>
      <Nav>
      </Nav>
    </Navbar.Collapse>
  </Navbar>
  <ArticlesPopUp title={this.state.currentArticleTitle} 
  body ={this.state.currentArticleBody} 
  viseble ={this.state.visableArticle} 
  click ={()=> this.loadArticle(0)}
  />
  </div>
    );
  }
}

 