import React, { Component } from 'react';
import '../ArticlesPopUp/ArticlesPopUp.css'


export class ArticlesPopUp extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
        
    }
    ArticleViseble() {
        if(this.props.viseble === true)
        {
            return "ArticleContainer show"
        }
        else
        {
            return "ArticleContainer"
        }   
    }
    
    render() { 
        return (
            
            <div className={this.ArticleViseble()} >
                <button onClick ={()=> {
                    this.props.click();
                }}>exit</button>
              <div className="Article">
                  <h2>{this.props.title}</h2>
                  <div>
                     {this.props.body}
                  </div>
              </div>
            </div>
          );
    }
}
 